import { useMutation } from '@tanstack/react-query';
import axios from 'axios';
import React, { useState } from 'react';
import Postcode from 'react-daum-postcode';
import { useForm } from 'react-hook-form';
import { postOrder } from 'src/hooks/queries/order';
import StyledMain from './StyledMain';

export interface OrderType {
  name: string;
  phone: string;
  address: string;
  product: ProductType[];
  status?: string;
  price?: string;
}

interface ProductType {
  type: string;
  size: string;
  quantity: number;
}

const Main = () => {
  const blazerSizes = ['XS', 'S', 'M', 'L', 'XL', 'XXL'];
  const skirtSizes = ['S', 'M', 'L', 'XL'];

  const [option, setOption] = useState(new Map());
  const [visible, setVisible] = useState(false);

  const {
    register,
    getValues,
    setValue,
    formState: { errors },
  } = useForm({ mode: 'onChange' });

  const { mutate: orderMutate } = useMutation(postOrder, {
    onSuccess: () => {
      alert(' .');
    },
    onError: (err) => {
      console.log(err);
    },
  });

  //   
  const handleTotalPrice = () => {
    let blazerQuan = 0;
    let skirtQuan = 0;
    for (let item of option.values()) {
      if (item.quantity !== '') {
        if (item.type === 'blazer') blazerQuan += parseInt(item.quantity);
        if (item.type === 'skirt') skirtQuan += parseInt(item.quantity);
      }
    }
    return Number(blazerQuan * 139000 + skirtQuan * 59000);
  };

  // 
  const handleProduct = () => {
    let product: ProductType[] = [];
    Array.from(option.values()).reduce((acc, cur) => {
      product.push(cur);
    }, []);
    return product;
  };

  //  
  const handlePostcode = (data: any) => {
    let fullAddress = `(${data.zonecode}) ${data.address}`;
    // let extraAddress = '';

    // if (data.addressType === 'R') {
    //   if (data.bname !== '') {
    //     extraAddress += data.bname;
    //   }
    //   if (data.buildingName !== '') {
    //     extraAddress += extraAddress !== '' ? `, ${data.buildingName}` : data.buildingName;
    //   }
    //   fullAddress += extraAddress !== '' ? ` (${extraAddress})` : '';
    // }

    // console.log(fullAddress);
    setValue('address1', fullAddress);
    setVisible(false);
  };

  // 
  const onSubmit = async (data: any) => {
    const product = handleProduct();
    data = getValues();

    if (product.length > 0 && Object.keys(errors).length < 1) {
      // console.log(product);
      // console.log(data);
      orderMutate({
        name: data.name,
        phone: data.phone,
        address: data.address1 + ' ' + data.address2,
        product: product,
      });
      setOption(new Map());
    } else {
      alert('    .');
    }
  };

  return (
    <section css={StyledMain}>
      <div className="container">
        <h3>AUTUMN VIBE</h3>
        <h4> </h4>
        <div className="info">
          <div className="product-detail">
            <div className="product-title">  </div>
            <div className="product-content">
              <p>
                   . <br />
                   .       . <br />
                   .   .
              </p>
              <p>139,000</p>
              <img
                src="https://static.zara.net/photos///2022/I/0/1/p/2761/250/800/18/w/750/2761250800_2_5_1.jpg?ts=1664371662270"
                alt="blazer"
              />
            </div>
            <div className="product-option">
              {blazerSizes.map((size: string) => (
                <label key={size}>
                  {size}
                  <input
                    type="number"
                    placeholder=""
                    min={1}
                    max={100}
                    onChange={(e) => {
                      setOption(
                        (prev) =>
                          new Map([
                            ...prev,
                            [`balzer${size}`, { type: 'blazer', size: size, quantity: e.target.value }],
                          ]),
                      );
                    }}
                  />
                </label>
              ))}
            </div>
          </div>
          <div className="product-detail">
            <div className="product-title">  </div>
            <div className="product-content">
              <p>
                    . <br />
                  .  . <br />
                    .
              </p>
              <p>59,000</p>
              <img
                src="https://static.zara.net/photos///2022/I/0/1/p/2761/259/800/18/w/750/2761259800_1_1_1.jpg?ts=1664371662680"
                alt="skirt"
              />
            </div>
            <div className="product-option">
              {skirtSizes.map((size: string) => (
                <label key={size}>
                  {size}
                  <input
                    type="number"
                    placeholder=""
                    min={1}
                    max={100}
                    onChange={(e) => {
                      setOption(
                        (prev) =>
                          new Map([...prev, [`skirt${size}`, { type: 'skirt', size: size, quantity: e.target.value }]]),
                      );
                    }}
                  />
                </label>
              ))}
            </div>
          </div>
        </div>
        <h5>  : {handleTotalPrice().toLocaleString()}</h5>
        <div className="order">
          <h4> </h4>
          <input type="text" placeholder=" " {...register('name', { required: true })} />
          {errors.name && <div className="error-message"> .</div>}
          <input
            type="text"
            placeholder="  ( )"
            {...register('phone', { pattern: /(^02.{0}|^01.{1}|[0-9]{3})([0-9]+)([0-9]{4})/g })}
          />
          {errors.phone && <div className="error-message"> .</div>}
          <div className="row">
            <input type="text" placeholder=" " readOnly {...register('address1')} />
            <button type="button" className="postcode-btn" onClick={() => setVisible(true)}>
               
            </button>
          </div>
          {visible && (
            <div className="postalcode-wrapper">
              <Postcode onComplete={handlePostcode} />
            </div>
          )}
          <input type="text" placeholder=" " {...register('address2')} />
          <button type="submit" onClick={onSubmit}>
            
          </button>
        </div>
      </div>
    </section>
  );
};

export default Main;
